import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../../../layouts/toast-notification/toast.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./destination-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class DestinationAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 destinationForm: FormGroup;
	 destinationId = 0;  
	 status;
	 destination= new DestinationData(); 
	 bootstrapFormValues = [];
	 msg = [];
 


 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.destinationId).subscribe(params=>{
			if(params.destinationId != undefined){
				this.destinationId=params.destinationId; 
				}
		}); 
	
	if(this.destinationId == 0){
		this.status="Add";
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.destinationForm = new FormGroup({			
			'destinationId' : new FormControl(''),
			'destinationName' : new FormControl('', [ Validators.required]),
		    'destinationEntryId' : new FormControl(''),
			'destinationEntryDate' : new FormControl(''),
			'destinationModifiedId' : new FormControl(''),
			'destinationModifiedDate' : new FormControl(''),
		});
  }
	  
	    get destinationName(){ return this.destinationForm.get('destinationName')};
	    get destinationDesc(){ return this.destinationForm.get('destinationDesc')};
	    get destinationEntryId(){ return this.destinationForm.get('destinationEntryId')};
	    get destinationEntryDate(){ return this.destinationForm.get('destinationEntryDate')};
	    get destinationModifiedId(){ return this.destinationForm.get('destinationModifiedId')};
	    get destinationModifiedDate(){ return this.destinationForm.get('destinationModifiedDate')};

	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/destination-data',
									JSON.stringify({ destinationId :this.destinationId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.destination = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['portal/destination-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.destination[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		 if(!this.destinationForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.destinationId == 0){ 
			this.destination['requestType'] = 'add';
		}else{
			this.destination['requestType'] = 'update';
			this.destination['destinationId'] = this.destinationId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/destination',
									JSON.stringify(this.destination),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.destinationForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
			
			}
	  }


  onDelete(){
		 this.destination['requestType'] = 'delete';
		 this.destination['destinationId'] = this.destinationId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/destination',
									JSON.stringify(this.destination),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.destinationForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
}

export class DestinationData {
	
	destinationId: string ;
	destinationName: string;
    destinationEntryId:string;
	destinationEntryBy:string;
	destinationEntryDate:string;
    destinationModifiedId:string;
    destinationModifiedBy:string;
    destinationModifiedDate:string;
}



